import React, { Component } from "react";

export default class GoogleAd extends Component {
  /**
   * 渲染
   */
  render() {
    return (
      <>
        <script
          async
          src={`https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=${process.env.REACT_APP_GOOGLE_AD_CLIENT}`}
          crossOrigin="anonymous"
        ></script>
        <ins
          className={`${this.props.className} adsbygoogle`}
          style={this.props.style}
          data-ad-client={this.props.client}
          data-ad-slot={this.props.slot}
          data-ad-layout={this.props.layout}
          data-ad-layout-key={this.props.layoutKey}
          data-ad-format={this.props.format}
          data-full-width-responsive={this.props.responsive}
        ></ins>
        <script>
          (window.adsbygoogle = window.adsbygoogle || []).push({});
        </script>
      </>
    );
  }
}

/**
 * 默认值
 */
GoogleAd.defaultProps = {
  className: "",
  style: { display: "block", with: "100%", height: "100px" },
  format: "auto",
  layout: "",
  layoutKey: "",
  responsive: "false",
};
